@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
*{
    margin: 0px;
    padding: 0px;
}

body{
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	       'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	       sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size:xx-large;

}


.context {
    width: 100%;
    position: absolute;
    top:50vh;
    
}

.context h1{
    text-align: center;
    color: #fff;
    font-size: 50px;
}


.area{
    position: absolute;
    background: #4e54c8;  
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);  
    width: 100%;
}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 40s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animate {
    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-7350px) rotate(720deg);
	opacity: 0;
        border-radius: 50%;
    }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.word_list {
    display: flex;
    justify-content: space-around;    
    width: 100%;
    margin: 2em auto;
    text-align: left;
}

.word_list .ol_label {
    color: lime;
}


@keyframes click-wave {
    0% {
	position: relative;
	width: 30px;
	height: 30px;
	opacity: 0.35;
    }
    100% {
	width: 200px;
	height: 200px;
	margin-top: -80px;
	margin-left: -80px;
	margin-right:-80px;
	opacity: 0;
    }
}
.word_list .option-input02 {
      position: relative;
      display: flex;
      justify-content: space-around;
      top: -33px;
      margin-left: 240px;
      
      right: 0;
      bottom: 0;
      left: 0;
      width: 30px;
      height: 30px;
      margin-right: 0.5rem;
      cursor: pointer;
      transition: all 0.15s ease-out 0s;
      color: #ffffff;
      border: none;
      outline: none;
      background: #d7cbcb;
      -webkit-appearance: none;
      -moz-appearance: none;
           appearance: none;
}
.word_list .option-input02:hover {
    background: #99FF99;
}
.word_list .option-input02:checked {
    background: lime;
}
.word_list .option-input02:checked::before {
    font-size: 20px;
    line-height: 30px;
    position: absolute;
    display: inline-block;
    width: 30px;
    height: 30px;
    content: '✔';
    text-align: center;
}
.word_list .option-input02:checked::after {
    position: relative;
    display: block;
    content: '';
    animation: click-wave 0.65s;
    background: lime;
}
.word_list .option-input02.radio {
    border-radius: 50%;
}
.word_list .option-input02.radio::after {
    border-radius: 50%;
}
.word_list label {
    line-height: 40px;
    display: block;
}
.word_list .option-input02:disabled {
    cursor: not-allowed;
    background: #b8b7b7;
}
.word_list .option-input02:disabled::before {
    font-size: 20px;
    line-height: 30px;
    position: absolute;
    display: inline-block;
    width: 30px;
    height: 30px;
    content: '✖︎';
    text-align: center;
}

